.class-container
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.class
{
    position: relative;
    height: 150px;
    min-width: 200px;
    background-color: #ffffff;
    margin: 16px 16px 16px 0;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    cursor: pointer;
    border-radius: 4px;
    padding: 8px;
}